import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Toaster, toast } from 'react-hot-toast';
import NavigationBar from './components/NavigationBar';
import SearchBar, { SearchCriteria } from './components/SearchBar';
import TransactionTable from './components/TransactionTable';
import LoginFormModal from './components/LoginFormModal';
import LoadingModal from './components/LoadingModal';
import { getTransactionsByTime, downloadExcel } from './services/api';
import { TransactionRecord, User } from './types';
import { getStoredUser, storeUser, clearUser } from './services/auth';
import './config/i18n';

function App() {
  const { i18n, t } = useTranslation();
  const [transactions, setTransactions] = useState<TransactionRecord[]>([]);
  const [loading, setLoading] = useState(false);
  const [exportLoading, setExportLoading] = useState(false);
  const [hasSearched, setHasSearched] = useState(false);
  const [currentSearchCriteria, setCurrentSearchCriteria] = useState<SearchCriteria | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [showLoginModal, setShowLoginModal] = useState(false);

  useEffect(() => {
    const storedUser = getStoredUser();
    if (storedUser) {
      setUser(storedUser);
    } else {
      setShowLoginModal(true);
    }
  }, []);

  const handleSearch = async (criteria: SearchCriteria) => {
    setLoading(true);
    setHasSearched(true);
    setCurrentSearchCriteria(criteria);
    try {
      const data = await getTransactionsByTime(
        criteria.timeType,
        criteria.startDate,
        criteria.endDate,
        [],
        []
      );

      const filteredData = criteria.searchValue
        ? data.filter((record) =>
            String(record[criteria.filterType as keyof TransactionRecord])
              .toLowerCase()
              .includes(criteria.searchValue.toLowerCase())
          )
        : data;

      setTransactions(filteredData);

      if (filteredData.length === 0) {
        toast.error(t('No data available'));
      }
    } catch (error) {
      console.log(error);
      toast.error(t('Error'));
    } finally {
      setLoading(false);
    }
  };

  const handleExport = async () => {
    if (!currentSearchCriteria) return;
    setExportLoading(true);
    try {
      await downloadExcel(
        transactions,
        currentSearchCriteria.startDate,
        currentSearchCriteria.endDate
      );
    } catch (error) {
      toast.error(t('Error'));
    } finally {
      setExportLoading(false);
    }
  };

  const toggleLanguage = () => {
    i18n.changeLanguage(i18n.language === 'en' ? 'zh' : 'en');
  };

  const handleLogin = (userData: User) => {
    setUser(userData);
    storeUser(userData);
    setShowLoginModal(false);
  };

  const handleLogout = () => {
    setUser(null);
    clearUser();
    setShowLoginModal(true);
    setTransactions([]);
    setHasSearched(false);
  };

  if (!user) {
    return (
      <>
        <Toaster position="top-right" />
        <LoginFormModal isOpen={showLoginModal} onLogin={handleLogin} />
      </>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <Toaster position="top-right" />
      <LoadingModal isOpen={exportLoading} />
      <NavigationBar 
        onLanguageToggle={toggleLanguage} 
        onLogout={handleLogout}
        userName={user.Name}
        userEmployer={user.Employer}
      />

      <main className="max-w-7xl mx-auto py-8 px-4 sm:px-6 lg:px-8 space-y-6">
        <SearchBar onSearch={handleSearch} />

        {loading ? (
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          </div>
        ) : transactions.length > 0 ? (
          <TransactionTable data={transactions} onExport={handleExport} />
        ) : hasSearched ? (
          <div className="bg-white rounded-lg shadow-lg p-6">
            <div className="text-center text-gray-700">
              {t('No transaction records')}
            </div>
          </div>
        ) : null}
      </main>
    </div>
  );
}

export default App;