import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  createColumnHelper,
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
  SortingState,
  useReactTable,
} from '@tanstack/react-table';
import { TransactionRecord } from '../types';
import { Download, ArrowUpDown, ArrowUp, ArrowDown } from 'lucide-react';

interface TransactionTableProps {
  data: TransactionRecord[];
  onExport: () => void;
}

const columnHelper = createColumnHelper<TransactionRecord>();

const TransactionTable: React.FC<TransactionTableProps> = ({ data, onExport }) => {
  const { t, i18n } = useTranslation();
  const [sorting, setSorting] = useState<SortingState>([]);
  const [currentPage, setCurrentPage] = useState(0);
  const [inputPage, setInputPage] = useState('');
  const pageSize = 20;

  const columns = [
    columnHelper.accessor(row => '', {
      id: 'index',
      header: () => i18n.language === 'en' ? 'Index' : '項次',
      cell: (info) => info.row.index + 1,
      enableSorting: false,
    }),
    columnHelper.accessor('CODE', {
      header: () => i18n.language === 'en' ? 'Drug Code' : '藥碼',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('NAME', {
      header: () => i18n.language === 'en' ? 'Drug Name' : '藥名',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('TXN_QTY', {
      header: () => i18n.language === 'en' ? 'Transaction Quantity' : '交易量',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('MED_BAG_NUM', {
      header: () => i18n.language === 'en' ? 'Medication Bag Number' : '領藥號',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('PHARER_NAME', {
      header: () => i18n.language === 'en' ? 'Operator' : '操作人',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('PATNAME', {
      header: () => i18n.language === 'en' ? 'Patient' : '病人',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('PATCODE', {
      header: () => i18n.language === 'en' ? 'Medical Record Number' : '病歷號',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('POST_TIME', {
      header: () => i18n.language === 'en' ? 'Operation Time' : '操作時間',
      cell: info => info.getValue(),
      enableSorting: true,
    }),
    columnHelper.accessor('CT_TIME', {
      header: () => i18n.language === 'en' ? 'Prescription Time' : '開方時間',
      cell: info => info.getValue(),
      enableSorting: true,
    })
  ];

  const table = useReactTable({
    data,
    columns,
    state: {
      sorting,
    },
    onSortingChange: setSorting,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    pageCount: Math.ceil(data.length / pageSize),
  });

  const pageCount = Math.ceil(data.length / pageSize);
  const start = currentPage * pageSize;
  const end = start + pageSize;
  const pageData = table.getRowModel().rows.slice(start, end);

  const handlePageInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) {
      setInputPage(value);
      const pageNumber = parseInt(value, 10);
      if (pageNumber >= 1 && pageNumber <= pageCount) {
        setCurrentPage(pageNumber - 1);
      }
    }
  };

  const handlePageInputBlur = () => {
    if (!inputPage || parseInt(inputPage, 10) < 1 || parseInt(inputPage, 10) > pageCount) {
      setInputPage('');
    } else {
      const pageNumber = parseInt(inputPage, 10);
      setCurrentPage(pageNumber - 1);
      setInputPage('');
    }
  };

  const getSortingIcon = (column: any) => {
    if (!column.getCanSort()) return null;
    
    if (column.getIsSorted() === 'asc') {
      return <ArrowUp className="inline-block ml-2 h-4 w-4" />;
    }
    if (column.getIsSorted() === 'desc') {
      return <ArrowDown className="inline-block ml-2 h-4 w-4" />;
    }
    return <ArrowUpDown className="inline-block ml-2 h-4 w-4" />;
  };

  return (
    <div className="bg-white rounded-lg shadow-lg">
      <div className="p-6 border-b border-gray-200 flex justify-between items-center">
        <div className="text-base text-gray-700">
          第 {currentPage * pageSize + 1}-{Math.min((currentPage + 1) * pageSize, data.length)} 筆，共 {data.length} 筆
        </div>
        <button
          onClick={onExport}
          className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors duration-200"
        >
          <Download className="h-5 w-5 mr-2" />
          {t('Export')}
        </button>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            {table.getHeaderGroups().map(headerGroup => (
              <tr key={headerGroup.id}>
                {headerGroup.headers.map(header => (
                  <th
                    key={header.id}
                    onClick={header.column.getCanSort() ? header.column.getToggleSortingHandler() : undefined}
                    className={`px-6 py-3 text-left text-xs font-medium text-gray-700 uppercase tracking-wider whitespace-nowrap ${
                      header.column.getCanSort() ? 'cursor-pointer hover:bg-gray-100' : ''
                    }`}
                    style={{ minWidth: '80px' }}
                  >
                    <div className="flex items-center">
                      {flexRender(
                        header.column.columnDef.header,
                        header.getContext()
                      )}
                      {getSortingIcon(header.column)}
                    </div>
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {pageData.map(row => (
              <tr key={row.id} className="hover:bg-gray-50">
                {row.getVisibleCells().map(cell => (
                  <td 
                    key={cell.id} 
                    className="px-6 py-4 text-base text-gray-700 whitespace-nowrap"
                    style={{ minWidth: '80px' }}
                  >
                    {flexRender(cell.column.columnDef.cell, cell.getContext())}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="px-6 py-4 border-t border-gray-200">
        <div className="flex items-center justify-between">
          <div className="text-base text-gray-700">
            第 {currentPage + 1} 頁，共 {pageCount} 頁
          </div>
          <div className="flex items-center space-x-4">
            <button
              onClick={() => setCurrentPage(prev => Math.max(0, prev - 1))}
              disabled={currentPage === 0}
              className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 disabled:bg-gray-100 disabled:text-gray-400 disabled:cursor-not-allowed"
            >
              上一頁
            </button>
            <div className="flex items-center space-x-2">
              <input
                type="text"
                value={inputPage}
                onChange={handlePageInputChange}
                onBlur={handlePageInputBlur}
                placeholder={String(currentPage + 1)}
                className="w-16 px-3 py-2 text-center border border-gray-300 rounded-lg text-base focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
              <span className="text-base text-gray-700">/ {pageCount}</span>
            </div>
            <button
              onClick={() => setCurrentPage(p => Math.min(pageCount - 1, p + 1))}
              disabled={currentPage === pageCount - 1}
              className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50 disabled:bg-gray-100 disabled:text-gray-400 disabled:cursor-not-allowed"
            >
              下一頁
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TransactionTable;