import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Home, LogOut } from 'lucide-react';
import { getHomepage } from '../services/config';

interface NavigationBarProps {
  onLanguageToggle: () => void;
  onLogout: () => void;
  userName: string;
  userEmployer: string;
}

const NavigationBar: React.FC<NavigationBarProps> = ({ 
  onLanguageToggle, 
  onLogout, 
  userName,
  userEmployer 
}) => {
  const { t, i18n } = useTranslation();
  const [homepage, setHomepage] = useState<string>('');

  useEffect(() => {
    getHomepage().then(url => {
      setHomepage(url);
    });
  }, []);

  return (
    <nav className="bg-transparent py-4">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between">
          <div className="flex items-center">
            <a href="../frontpage" className="text-gray-700 hover:text-gray-900 mr-2">
              <Home className="h-8 w-8" />
            </a>
            <div className="ml-4">
              <h1 className="text-2xl md:text-3xl font-semibold text-gray-800 mb-1">
                {t('Transaction Records')}
              </h1>
              <p className="text-sm text-gray-600">
                {userName} - {userEmployer}
              </p>
            </div>
          </div>
          <div className="flex items-center space-x-4">
            <button
              onClick={onLanguageToggle}
              className="inline-flex items-center px-4 py-2 border border-gray-300 rounded-lg text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            >
              <svg className="h-4 w-4 mr-2" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
              </svg>
              {i18n.language === 'en' ? 'English' : '繁體中文'}
            </button>
            <button
              onClick={onLogout}
              className="px-6 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors duration-200 flex items-center"
            >
              <LogOut className="h-4 w-4 mr-2" />
              {t('Logout')}
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default NavigationBar;