export async function loadConfig() {
  try {
    const response = await fetch('/config.txt');
    if (!response.ok) {
      throw new Error('Failed to load config.txt');
    }
    const text = await response.text();
    return JSON.parse(text);
  } catch (error) {
    console.error('Error loading config:', error);
    return {
      domain: import.meta.env.VITE_APP_DOMAIN,
      homepage: import.meta.env.VITE_APP_HOMEPAGE
    };
  }
}

let cachedConfig: { domain: string; homepage: string } | null = null;

export async function getDomain(): Promise<string> {
  if (!cachedConfig) {
    cachedConfig = await loadConfig();
  }
  return cachedConfig.domain;
}

export async function getHomepage(): Promise<string> {
  if (!cachedConfig) {
    cachedConfig = await loadConfig();
  }
  return cachedConfig.homepage;
}