import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Search, RotateCcw } from 'lucide-react';

interface SearchBarProps {
  onSearch: (criteria: SearchCriteria) => void;
}

export interface SearchCriteria {
  timeType: 'op_time' | 'rx_time';
  startDate: string;
  endDate: string;
  filterType: string;
  searchValue: string;
}

const filterTypes = [
  { name: 'Drug Code', value: 'CODE' },
  { name: 'Drug Name', value: 'NAME' },
  { name: 'Operator', value: 'PHARER_NAME' },
  { name: 'Patient Name', value: 'PATNAME' },
  { name: 'Medical Record Number', value: 'PATCODE' },
  { name: 'Medication Bag Number', value: 'MED_BAG_NUM' },
];

const getDefaultStartDate = (): string => {
  const date = new Date();
  date.setDate(date.getDate() - 14);
  return `${date.toISOString().slice(0, 10)}T00:00`;
};

const getDefaultEndDate = (): string => {
  const date = new Date();
  return `${date.toISOString().slice(0, 10)}T23:59`;
};

const SearchBar: React.FC<SearchBarProps> = ({ onSearch }) => {
  const { t } = useTranslation();
  const [timeType, setTimeType] = useState<'op_time' | 'rx_time'>('op_time');
  const [startDate, setStartDate] = useState(getDefaultStartDate());
  const [endDate, setEndDate] = useState(getDefaultEndDate());
  const [filterType, setFilterType] = useState(filterTypes[0].value);
  const [searchValue, setSearchValue] = useState('');

  const handleSearch = () => {
    onSearch({
      timeType,
      startDate: `${startDate}:00`,
      endDate: `${endDate}:59`,
      filterType,
      searchValue,
    });
  };

  const handleReset = () => {
    setTimeType('op_time');
    setStartDate(getDefaultStartDate());
    setEndDate(getDefaultEndDate());
    setFilterType(filterTypes[0].value);
    setSearchValue('');
  };

  return (
    <div className="bg-white rounded-lg shadow-lg p-6">
      <div className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <div>
            <label className="block text-base font-medium text-gray-700 mb-2">
              {t('Time Type')}
            </label>
            <select
              value={timeType}
              onChange={(e) => setTimeType(e.target.value as 'op_time' | 'rx_time')}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            >
              <option value="op_time">{t('Operation Time')}</option>
              <option value="rx_time">{t('Prescription Time')}</option>
            </select>
          </div>

          <div>
            <label className="block text-base font-medium text-gray-700 mb-2">
              {t('Start Date')}
            </label>
            <input
              type="datetime-local"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div>
            <label className="block text-base font-medium text-gray-700 mb-2">
              {t('End Date')}
            </label>
            <input
              type="datetime-local"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
        </div>

        <div className="flex gap-6">
          <div className="w-[30%]">
            <select
              value={filterType}
              onChange={(e) => setFilterType(e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            >
              {filterTypes.map((type) => (
                <option key={type.value} value={type.value}>
                  {t(type.name)}
                </option>
              ))}
            </select>
          </div>
          <div className="w-[70%]">
            <input
              type="text"
              value={searchValue}
              onChange={(e) => setSearchValue(e.target.value)}
              placeholder={t('Please enter search content')}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
        </div>
      </div>

      <div className="flex justify-center mt-6 space-x-4">
        <button
          onClick={handleSearch}
          className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors duration-200 flex items-center"
        >
          <Search className="h-4 w-4 mr-2" />
          {t('Search')}
        </button>
        <button
          onClick={handleReset}
          className="px-6 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors duration-200 flex items-center"
        >
          <RotateCcw className="h-4 w-4 mr-2" />
          {t('Reset')}
        </button>
      </div>
    </div>
  );
};

export default SearchBar;