import { ApiResponse, TransactionRecord } from '../types';
import { getDomain } from './config';

let API_URL: string | null = null;

async function getApiUrl() {
  if (!API_URL) {
    API_URL = await getDomain();
  }
  return API_URL;
}

async function fetchApi<T>(endpoint: string, data: { Data: any; ValueAry: any[] }): Promise<T> {
  const apiUrl = await getApiUrl();
  const url = `${apiUrl}${endpoint}`;
  
  console.group(`🌐 API Request: ${endpoint}`);
  console.log('URL:', url);
  console.log('Method: POST');
  console.log('Request Payload:', data);
  console.groupEnd();

  const response = await fetch(url, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  });

  if (!response.ok) {
    console.error(`❌ API Error: ${endpoint}`, {
      status: response.status,
      statusText: response.statusText,
    });
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  const responseData = await response.json();

  console.group(`✅ API Response: ${endpoint}`);
  console.log('Status:', response.status);
  console.log('Response Data:', responseData);
  console.groupEnd();

  return responseData;
}

export const getTransactionsByTime = async (
  timeType: 'op_time' | 'rx_time',
  startTime: string,
  endTime: string,
  stations: string[],
  stationTypes: string[]
): Promise<TransactionRecord[]> => {
  const endpoint = timeType === 'op_time' 
    ? '/api/orderT/get_by_post_time_st_end'
    : '/api/orderT/get_by_rx_time_st_end';

  try {
    console.group('📍 Getting Transactions');
    console.log('Time Type:', timeType);
    console.log('Date Range:', { startTime, endTime });

    const formatDateTime = (dateTimeStr: string): string => {
      return dateTimeStr.replace('T', ' ');
    };
    
    const response = await fetchApi<ApiResponse<TransactionRecord[]>>(endpoint, {
      Data: {},
      ValueAry: [
        formatDateTime(startTime),
        formatDateTime(endTime)
      ]
    });
    console.groupEnd();
    return response.Data;
  } catch (error) {
    console.error('❌ Error fetching transactions:', error);
    throw new Error('Failed to fetch transactions');
  }
};

export const downloadExcel = async (data: TransactionRecord[], startTime: string, endTime: string): Promise<void> => {
  const apiUrl = await getApiUrl();
  const url = `${apiUrl}/api/transactions/download_datas_excel`;
  
  try {
    console.group('📍 Downloading Excel');
    console.log('URL:', url);
    console.log('Method: POST');
    console.log('Date Range:', { startTime, endTime });
    console.log('Records Count:', data.length);

    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Accept': 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        Data: data,
        ValueAry: []
      }),
    });

    if (!response.ok) {
      console.error('❌ Download failed:', response.status, response.statusText);
      throw new Error(`Download failed: ${response.statusText}`);
    }

    const contentDisposition = response.headers.get('content-disposition');
    const formatDate = (dateStr: string) => dateStr.split('T')[0];
    let filename = `${formatDate(startTime)}-${formatDate(endTime)} Transaction Records.xlsx`;
    
    if (contentDisposition) {
      const matches = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(contentDisposition);
      if (matches != null && matches[1]) {
        filename = matches[1].replace(/['"]/g, '');
      }
    }

    const blob = await response.blob();
    
    if (!blob.type.includes('spreadsheet') && !blob.type.includes('excel')) {
      console.error('❌ Invalid file type received:', blob.type);
      throw new Error('Invalid file type received from server');
    }

    const downloadUrl = window.URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = downloadUrl;
    link.download = filename;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    window.URL.revokeObjectURL(downloadUrl);
    
    console.log('✅ Excel download completed');
    console.groupEnd();
  } catch (error) {
    console.error('❌ Error downloading Excel:', error);
    throw new Error('Failed to download Excel file');
  }
};